export default {
	data() {
		return {};
	},
	props: {
		// 模态框标题
		title: String,
		// 显示
		visible: {
			default: false
		},
		// 模态框宽度
		width: {
			default: 300
		},
		// 内容固定高度
		fixedHeight: {
			default: 180
		}
	},
	methods: {
		handleClose() {
			this.$emit('close');
		}
	}
};
